import React, { Component } from 'react';
import Comment from './Comment.component';
import { UserContext } from '../../../GeneralComponents/context/userContext';
import { sendData, apiKey} from '../../../GeneralComponents/AJAX/HttpRequest';
import { trackPromise } from 'react-promise-tracker';

class Comments extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            comment: ''
         }
    }

    static contextType = UserContext;

    handleUserInput = (event) => {
        event.preventDefault();
        const { target } = event;
        const { name, value } = target;
        this.setState({[name]: value},  ()=> {
            console.log(this.state)
        })
      }
    handleSubmit =(event) => {
        event.preventDefault();
        const { data: { userId, token } } = this.context;
        const { feedId, feedType } = this.props;
        const contentType = feedType === 'article' ? 'articles' : 'gifs';
        const reqBody = {
            comment: this.state.comment,
            userId: parseInt(userId, 10)
        }
        // trackPromise(
            sendData(`${apiKey}/${contentType}/${feedId}/comment`, reqBody, token)
            .then(res => {
                console.log(res);
            }).catch(err => {
                console.log(err)
            })
        // )
    }
    render() {
        const { data: { userId, token } } = this.context;
        const {feedId, comments, users, feedType } = this.props;
        console.log(comments);
        return (
            <div className="comment--section">
                {comments.map((comment, index) => <Comment key={index} comment={comment} feedId={feedId} feedType={feedType} users={users} userData={{userId, token}} />)}
                {/* <Comment commentId={} />
                <Comment />
                <Comment /> */}
                <form className="form d-flex position-relative" style={{width: '100%'}}>
                    <textarea className="textarea padding-md padding-bottom-lg padding-right-lg font-md margin-bottom-sm"
                        onChange={(event) => this.handleUserInput(event)} name="comment"
                        value={this.state.comment} placeholder="Post Comment">
                    </textarea>
                    <i className="fas fa-paper-plane color2 font-lg"
                        style={{position: 'absolute', bottom: '20px', right: '25px'}}
                        onClick={(event) => this.handleSubmit(event)}>
                    </i>
                </form>
            </div>
         );
    }
}
 
export default Comments;